import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

export default new Vuex.Store({
    state: {
        todos: []
    },
    mutations: {
        addToDo(state, todo) {
            state.todos.push(todo);
            console.log(state.todos);
        }
        ,
        deleteToDo(state,id){
            // const newToDo = this.todos.filter((todo) => todo.id !== index);
            const newToDo = state.todos.filter( (todo) => todo.id!==id);
            state.todos = newToDo;
        }
    }
})
