<template>
  <v-form v-model="valid" v-on:submit.prevent="addtodo">
    <v-alert type="success" v-if="snackbar===true">{{text}}</v-alert>
    <v-text-field
      v-model="message"
      :rules="nameRules"
      label="Message"
      required
    ></v-text-field>
    <v-text-field
      v-model.lazy="firstname"
      label="firstName"
      :rules="nameRules"
      required
    />
    <v-text-field
      v-model.lazy="lastname"
      label="lastName"
      :rules="nameRules"
      required
    />
    <v-text-field
      v-model.lazy="phnumber"
      label="phNumber"
      :rules="nameRules"
      required
    />

    <v-btn color="primary" class="addButton" type="submit"> Add </v-btn>
    <!-- <v-snackbar v-model="snackbar">
      {{ text }}

      <template v-slot:action="{ attrs }">
        <v-btn color="pink" text v-bind="attrs" @click="snackbar = false">
          Close
        </v-btn>
      </template>
    </v-snackbar> -->
  </v-form>

  <!-- <form v-on:submit.prevent="addtodo">
      <input type="text" v-model="message" />
      <button type="submit">Add</button>
      <p>{{ message }}</p>
    </form> -->
</template>
<script>
export default {
  data: () => ({
    snackbar: false,
    text: `Added Successfully Goto Contact List`,
    message: "",
    firstname: "",
    lastname: "",
    phnumber: "",
    valid: false,
    nameRules: [(v) => !!v || "Message is required"],
  }),
  methods: {
    addtodo: function () {
      const newObj = {
        id: Math.round(Math.random() * Math.floor(10)),
        message: this.message,
        firstname: this.firstname,
        lastname: this.lastname,
        phnumber: this.phnumber,
        seen: false,
      };
      // this.todos.push({ text: this.message, seen: false });
      this.snackbar = true;
      console.log("newObj", newObj);
      this.message = "";
      // this.$router.push({
      //   name:"Contact",
      //   params:{
      //     todo:newObj
      //   }
      // })
      this.$emit("add-todo", newObj);
    },
  },
};
</script>
<style>
.addButton {
  /* background-color: brown !important; */
}
</style>