<template>
  <v-simple-table>
    <template v-slot:default>
      <thead>
        <tr>
          <th class="text-left">First Name</th>
          <th class="text-left">Last name</th>
          <th class="text-left">Message</th>
          <th class="text-left">Phone Number</th>
          <th></th>
          <th></th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="item in todos" :key="item.id">
          <td>{{ item.firstname }}</td>
          <td>{{ item.lastname }}</td>
          <td>{{ item.message }}</td>
          <td>{{ item.phnumber }}</td>
          <td><EditModal :item="item" /></td>
          <td><DeleteToDo :item="item" v-on:delete-todo="deletetodo" /></td>
        </tr>
      </tbody>
    </template>
  </v-simple-table>
</template>
<script>
import EditModal from "./EditModal";
import DeleteToDo from "./DeleteToDo";
export default {
  components: {
    EditModal,
    DeleteToDo,
  },
  methods: {
    deletetodo: function (id) {
      this.$store.commit("deleteToDo", id);
    }
  },
    computed: {
    todos() {
      console.log("contactlist component", this.$store.state.todos);
      return this.$store.state.todos;
    },
  },
  data() {
    return {
      group: [
        {
          firstname: "dasd",
          id: 5,
          lastname: "sad",
          message: "das",
          phnumber: "asd",
          seen: false,
        },
      ],
    };
  },
};
</script>
