import Vue from 'vue';
import Router from 'vue-router';
import ToDos from '../components/ToDos';
import ContactList from '../components/ContactList';

Vue.use(Router);

export default new Router({
    routes: [{
        path: "/",
        name: "Todoa",
        component: ToDos
    },
    {
        path: "/list",
        name: "Contact",
        component: ContactList
    },]
})