<template>
  <div class="text-center">
    <v-dialog v-model="dialog" width="500">
      <template v-slot:activator="{ on, attrs }">
        <v-btn color="red lighten-2" dark v-bind="attrs" v-on="on">
         Edit   
        </v-btn>
      </template>

      <v-card>
        <v-card-title class="headline grey lighten-2">
         Edit contact
        </v-card-title>
        <v-card-text>
          <v-form >
            <v-text-field
              v-model.lazy="item.message"
              label="Message"
              required
            />
            <v-text-field
              v-model.lazy="item.firstname"
              label="firstName"
              required
            />
            <v-text-field
              v-model="item.lastname"
              label="lastName"
              required
            />
            <v-text-field
              v-model="item.phnumber"
              label="phNumber"
              required
            />
            <v-btn color="primary" class="addButton" type="submit" @click="dialog = false">
              Save
            </v-btn>
          </v-form>
        </v-card-text>

        <v-divider></v-divider>

        <!-- <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="primary" text @click="dialog = false"> I accept </v-btn>
        </v-card-actions> -->
      </v-card>
    </v-dialog>
  </div>
</template>
<script>
export default {
  props: ["item"],
  data() {
    return {
      dialog: false,
    };
  },
};
</script>